import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
import './header.styles.scss';
import sally from '../../Sally.png';

const diffInDays = (date1, date2) => {
    return Math.floor((date1.getTime()- date2.getTime()) / (1000 * 3600 * 24)); 
}

const birth = new Date('1980-04-19'); 
const today = new Date(); 
const dayOfYear = (today.getMonth() + 1) + "-" + (today.getDate());
const ageDays = diffInDays(today, birth); 
const ageYears = today.getFullYear() - birth.getFullYear()


let greeting = '';

switch(dayOfYear) {
    case "4-19":
        greeting = 'Sally turns ' + ageYears + ' today, happy birthday Sally :)'
        break;
    case "4-20":
        greeting = 'Sally turned ' + ageYears + ' yesterday, happy birthday Sally :)'
        break;
    case "4-21":
        greeting = 'Sally turned ' + ageYears + ' 2 days ago, happy birthday Sally :)'
        break;
    default:
        greeting = 'Sally is ' + ageDays + ' days young today, happy day Sally :)'
    }


class Header extends Component {

    render() { 

        return (
            <div className='header'>   
                <img className='logo' src={sally} alt="logo"/>
                <h1 className='greeting'>{greeting}</h1>
            </div>            
        )
    }
}

export default Header;